<template>
<div>
 <main class="container-fluid">
   <h1 class="text-center">全台即時天氣 Weather In Taiwan</h1>
   <div class="row">
     <div class="map" :style = '[ data.length >1 ? {backgroundImage:`url(${pic})`} :{backgroundImage:``}]'>
       <svg></svg>
       <div class="mapInfo"  v-if="data.length>1">
         <h4 >Weather in {{cityName}}</h4>
         <p class="weatherInfo mb-1">今日氣象</p>
         <p class="weatherInfo mb-1">天氣：{{filterName[0].weather[0].description}} <img :src="`http://openweathermap.org/img/w/${filterName[0].weather[0].icon}.png`" alt=""></p>
         <p class="weatherInfo mb-1">最高溫：{{filterName[0].main.temp_max}}°C</p>
         <p class="weatherInfo mb-1">最低溫：{{filterName[0].main.temp_min}}°C</p>
         <p class="weatherInfo mb-1">平均溫：{{filterName[0].main.temp}}°C</p>
         <p class="weatherInfo mb-1">風速：{{filterName[0].wind.speed}}</p>
       </div>
     </div>
    <div>
    </div>
   </div>
 </main>
 <section class="container-fluid" id="c3-taiwan">
  <div class="row ">
    <div class="col-8 mx-auto">
      <h4 class="text-center mb-5">全台各地天氣預報</h4>
      <form class="mb-3">
        <div class="form-row">
          <div class="col-4 select">
            <select id="" v-model="c3Taiwan.id">
              <option :value="item.name_id" v-for="item in taiwan" :key="item.name">{{item.name}}</option>
            </select>
          </div>
          <div class="col-4 select ml-2">
           <select  id="" v-model="c3Taiwan.cnt">
              <option value="8">一天</option>
              <option value="16">兩天</option>
              <option value="24">三天</option>
              <option value="32">四天</option>
              <option value="40">五天</option>
            </select>
          </div>
          <div class="col-2">
            <button type="submit" class="form-control btn btn-primary" placeholder="Zip" @click.prevent="getForecast">送出</button>
          </div>
        </div>
      </form>

    </div>
    <div class="col-12">
      <div id="chart"></div>
    </div>
   </div>
  </section>
</div>
</template>

