  },
  {
    name: "彰化縣",
    engname: "Chang-hua",
    pic: "https://pic.pimg.tw/car0126/1526664097-3738108602.jpg?v=1526664128",
    name_id:1679136
  },
  {
    name: "金門縣",
    engname: "Xiguoshan",
    pic: "https://kinmen.travel/image/10363/1024x768",
    name_id:1675247
  }
];
export default {
  name: "HelloWorld",
  data() {
    return {
      cityName: "" || "台灣",
      data: [],
      taiwan: taiwan,
      pic: "",
      c3Taiwan:{
        id:null || taiwan[0].name_id,
        cnt:8
      }
    };
  },
  computed: {
    filterName() {
      const vm = this;
      let city = [];
      let cityName = vm.taiwan.filter(item => {
        if (item.name == vm.cityName) {
          vm.pic = item.pic;
          return item;
        }
      });
      city = vm.data.filter(item => {
        if (cityName[0].engname == item.name) {
          return item;
        }
      });
      return city;
    }
  },
  methods: {
   getMap() {
    let vm = this;
    var projection = d3.geoMercator().center([121, 24]).scale(8000)    
    var path = d3.geoPath().projection(projection);
      
    d3.json("@/../static/taiwan.json", function(error, topology) {
      if (error) throw error;
    
      d3.select(".map svg").selectAll("path")
        .data(topojson.feature(topology, topology.objects.County_MOI_1070516).features)
        .enter().append("path")
        .attr("d", path)
        .attr( "name", function(d) {
            return d.properties["COUNTYNAME"];
          },)
        .attr("fill", "rgba(24,81,18,0.85)")
        .attr("stroke","rgba(255, 255, 255, 0.7)")
        .attr("stroke-width","2")
        .on("mouseenter", function(d) {
            d3.select(this)
            .attr("fill", "rgba(24,81,18,0.55)");
              vm.cityName = d.properties.COUNTYNAME;
        })
        .on("mouseleave", function(d) {
          d3.select(this).attr("fill","rgba(24,81,18,0.85)");
        });
    });
   },
   getData() {
    const vm = this;
    this.axios
      .get(
        "https://api.openweathermap.org/data/2.5/group?id=1668284,6724654,7280290,1675151,1668399,7280289,1674197,1667905,1671971,1671564,1665194,1668352,1670479,1668295,1674502,1670651,1675247,1678836,1679136&lang=zh_TW&units=metric&APPID=fe5145f9c52ca061eec3a0ce448c8864"
      )
      .then(res => {
        console.log(res.data);
        vm.data = [...res.data.list];
        console.log("vm.data", vm.data);
      })
      .catch(err => {
        console.log(err);
      });
   },
   getForecast(){
      const vm = this;
      let Cid =vm.c3Taiwan.id;
      let cnt =vm.c3Taiwan.cnt;
      let Maxtemp = '最高溫';
      let Mintemp = '最低溫';
      let timestemp= "x";
      let c3_Info = [];
      let c3_Info_Maxtemp=[];
      let c3_Info_Mintemp=[];
      let c3_Info_time = [];
      this.axios
      .get('https://api.openweathermap.org/data/2.5/forecast?id='+vm.c3Taiwan.id+'&lang=zh_TW&units=metric&appid=fe5145f9c52ca061eec3a0ce448c8864&cnt='+cnt+'')
      .then(res=>{
         c3_Info = res.data.list;
         c3_Info.forEach((item)=>{
           c3_Info_Maxtemp.push(item.main.temp_max);
           c3_Info_Mintemp.push(item.main.temp_min);
           c3_Info_time.push(item.dt_txt);
         });
         Maxtemp = [Maxtemp,...c3_Info_Maxtemp];
         Mintemp = [Mintemp,...c3_Info_Mintemp];
         timestemp = [timestemp,...c3_Info_time];
      })
      .then(()=>{
        c3.generate({
        bindto: "#chart",
        data: {
          type: 'spline',
          x: 'x',
          xFormat: "%Y-%m-%d %H:%M:%S",
          columns: [timestemp,Maxtemp,Mintemp],
          axes: {
            Maxtemp: 'y',
         }
        },
        axis: {
          x: {
            type: 'timeseries',
            tick: {
                format: '%Y-%m-%d %H:%M',
            },
            label: {
                text: '時間',
                position: 'outer-center'
            }
          },
          y:{
            label: {
              text: 'Y Label',
              position: 'outer-middle'
            },
            tick:{
              format:function(d){return d+'°C'}
            }
          },
          y2:{
            show:true,
             label: {
                text: 'Y2 Label',
                position: 'outer-middle'
            }
          }
        }
       });
      })
      .catch(err=>{
        console.log(err);
      })
    }
   },
  mounted() {
    this.getData();
    this.getMap();
  }
};

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
@import url("https://fonts.googleapis.com/css?family=Roboto:500");
@import url("https://fonts.googleapis.com/earlyaccess/cwtexyen.css");
h1{
  position: relative;
  z-index: 99;
  font-weight: 300;
  font-family: 'cwTeXYen', sans-serif;
