}
main {
  position: relative;
  min-height: 120vh;
  background-image: url("~@/assets/images/bg-kv3.jpg");
  background-position: center;
  background-size: cover;
  z-index: 1;
  &::after {
    content: "";
    display: block;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 2;
    background: #000;
    right: 0;
    top: 0;
    background: radial-gradient(
      ellipse at center,
      rgba(255, 255, 255, 1) 0%,
      rgba(255, 255, 255, 0) 95%,
      rgba(255, 255, 255, 0) 100%
    );
  }
  .mapInfo {
    letter-spacing: 1.8px;
    font-size: 23px;
    .weatherInfo {
      font-size: 20px;
    }
  }
}

.map {
  background: linear-gradient(
    45deg,
    rgba(255, 241, 235, 0.69) 0%,
    rgba(236, 229, 234, 0.88) 10%,
    rgba(216, 215, 232, 0.89) 21%,
    rgba(205, 219, 242, 0.95) 62%,
    rgba(213, 235, 251, 0.98) 83%,
    rgba(181, 255, 255, 1) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  border-radius: 10px 10px 10px 10px;
  height: 600px;
  margin: 100px auto 0 auto;
  position: relative;
  z-index: 100;
  box-shadow: inset 1px -2px 9px 1px #2a6fb2, -4px 7px 16px 2px #0a2b358c,
    -9px 6px 16px 2px #5ea4e3d9;
  background-size: cover;
}
@media (min-width: 1250px) {
  .map {
    width: 800px;
  }
  .mapInfo {
    position: absolute;
    right: -30%;
    top: 20%;
  }
   .weatherInfo{
     padding-left: 25px;
   }
}
@media (max-width: 1250px) {
  .map {
    width: 100%;
  }
  .mapInfo {
    width: 250px;
    margin: auto;
  }
  .weatherInfo{
     padding-left: 50px;
   }
}
svg {
  position: relative;
  z-index: 99;
  width: 100%;
  height: 100%;
}

#c3-taiwan {
  position: relative;
  z-index: 98;
  padding-top: 50px;
  background: linear-gradient(
      45deg,
      rgba(255, 241, 235, 0.69) 0%,
      rgba(236, 229, 234, 0.88) 10%,
      rgba(216, 215, 232, 0.89) 21%,
      rgba(205, 219, 242, 0.95) 62%,
      rgba(213, 235, 251, 0.98) 83%,
      rgba(181, 255, 255, 1) 100%
  );
  min-height: 70vh;
}

.select {
  position: relative;
  display: block;
  background: #859eb6;
  overflow: hidden;
  border-radius: .25em;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #859eb6;
  background-image: none;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0 0 0 .5em;
  color: #fff;
  cursor: pointer;
}
.select::after {
  content: '\25BC';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 5px 1em;
  background: #859eb6;
  pointer-events: none;
}
/* Transition */
.select:hover::after {
  color: #f39c12;
}
.select::after {
  -webkit-transition: .25s all ease;
  -o-transition: .25s all ease;
  transition: .25s all ease;
}
</style>



