<template>
  <footer class="container-fluid">
    <div class="container text-center py-3">
      <p class="m-0"> © Product made by Joe Chen</p>
      <p class="mb-0">opensource from <a href="https://openweathermap.org/">openweathermap</a> </p>
    </div>
    <div>
      <div id="chart"></div>
    </div>
  </footer>
</template>


<script>
import c3 from 'c3';
export default {
  name:'footer-content',
  mounted(){
      c3.generate({
      bindto: '#chart',
      data: {
        columns: [
          ['data1', 30, 200, 100, 400, 150, 250],
          ['data2', 50, 20, 10, 40, 15, 25]
        ]
      },
    });
  }
}
</script>

<style>

footer{
background-image: linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%);
}
</style>


